@import url(https://fonts.googleapis.com/css?family=Raleway:400,700,900);

body {
  font-family:'Raleway',sans-serif;
}

h2,h3 {
  color:#525252;
  line-height: 1.5;
}
h3 {
 font-size:1em;
 padding:0;
}

.btn.bg-green {
  width:100px;
  background-color:#0AA550;
  border:2px solid #0AA550;
}

.btn.bg-green:hover {
  background-color:white;
  color: #0AA550;
}

.grid-title {
  margin-top:1em;
  font-size:2.5em;
  font-weight:900;
  text-transform: uppercase;
}

.wrapper{
  padding: 2em 2.5em;
}

@media (max-width: 768px) { 
  .wrapper{
    padding: 2em 1.5em;
  }
}

.text-box {
  margin:1.5em 0;
  text-align: justify;
}
.btn-add-to-cart{
    width:130px;
    background-color:#e85d4a;
    border-color:#e85d4a;
}

.btnStyles:hover{
    color:white;
    background-color:#7fe6e6
}

.btnStyles {
    border-radius: 0;
    width:100px;
    color:white;
    background-color:#00cece;
}
.incrementWrapper .qtyBtn {
    width: 40px;
    height: 40px;
    line-height: 25px;
}
.incrementWrapper span {
    display:inline-block;
    text-align:center;
    width: 18px;
    font-size: 16px;
}

.incrementWrapper {
    width:100%;
}

.qtyBtn{
    background-color:#999999;
    border: 1px solid #eee;
}

#sidebar-cart .incrementWrapper .qtyBtn {
    width:35px;
    height:35px;
    line-height: 22px;
}
.card.product .card-img-top{
    max-width:100%;
    height:auto;
    width:auto;
    max-height:100%;
}
.card.product{
    border:0;
}
.card.product .card-body{
    text-align: center;
    padding: 1.25rem 0.1rem;
}


.card.product .card-body .img-wrapper {
    height:140px;
}

.card.product .content-wrapper{
    height:230px;
}

.card-body .price {
    font-size:1rem;
}

.card.product .card-title {
    margin:0;
}

.card.product .card-text {
    margin:0;
}

.card.product .card-text.wgth {
    font-size:0.8em;
}
.navbar-brand img{
    width:150px;
}

header {
    background-color:#fff;
    border-bottom:1px solid #eee;
}

#sidebar-cart {
    position: fixed;
    z-index: 1000;
    min-height: 100vh;
    top: 0;
    right: -500px;
    background: #FFF;
    width: 400px;
    max-width:98%;
    -webkit-transition: 0.3s ease-in;
    transition: 0.3s ease-in;
    box-shadow: -1px 0px 2px 1px #aaaaaa;
}



#sidebar-cart.show {
    -webkit-transition: 0.3s ease-in;
    transition: 0.3s ease-in;
    right:0;
} 

.close-sidebar-cart{
    cursor: pointer;
    
}
span{
    padding:0.25em;
}
.inpage-cart-button{
    padding:1em 1em;
}
.inpage-cart-button .text {
    font-size:1em;
}

@media (max-width: 768px) { 
    .inpage-cart-button .text {
        display:none;
    }
}
th img {
    width:70px;
}

.incrementer-wrapper {
    width:100px;
}


@media (max-width: 768px) { 
    #sidebar-cart th img, #checkout-orderlist th img {
        max-width:50px;
    }
 }
.footer-container{
    padding: 2em 0;
    background-color:rgba(0, 0, 0, 0.85);
}

/*TODO: Por quÃ© se rompe si no se quita el margen ?*/
@media (max-width: 768px) { 
    .footer-container > .row {
        margin:0;
    }
 }

.footer-logo{
    width:150px;
}

.footer-list,.footer-list a{
    list-style-type: none;
    color:white;
}


.sub-footer-container{
    padding-top:1em;
    font-size:0.8em;
    background-color:rgb(0,0,0);
    color:white;
}
#banner h2{
    font-weight:900;
    font-size:4em;
    color:white;
    line-height: 1.1;
    margin-top:.5em;
}

#banner p{
    line-height:1.1;
    font-weight:700;
    font-size:2em;
    color:white;
}

@media (max-width: 768px) { 
    #banner h2{
    font-weight:900;
    font-size:2em;
    color:white;
    line-height: 1.1;
    margin-top:.5em;
}

#banner p{
    line-height:1.1;
    font-weight:700;
    font-size:1.25em;
    color:white;
}
 }

.banner-wrapper:before {
        background-color: rgba(0,0,0,0.3);
        position: absolute;
        content:" ";
        top:0;
        left:0;
        width:100%;
        height:100%;
        display: block;
        z-index:0;
}


div #app{
    background-color:grey;
    background-image:url('/background.jpg');
    min-height: 100vh;
}

#content {
    background-color:#fff;
    min-height:100vh;
}

#content.container {
    padding:0;
}
.single-date {
    text-align: center;
    padding:1em 0.75em;
    margin-bottom:1em;
    border:1px solid black;
    border-radius:5px;
    cursor: pointer;
    height:70px;
    width:220px;
    max-width:100%;
}

.single-date:hover{
    background-color:grey;
}

.single-date:focus{
}

.single-date p {
    padding:0;
    margin:0;
}
.legal{
    list-style-type: decimal;
}

.box-wrapper{
    padding:1em 0;
}
